import React from "react";
import PropTypes from "prop-types";

function LessonItem({ lesson, onZoom, onModal }) {
  const payload = {
    data: {
      name: lesson.name,
      video: lesson.video
    }
  };
  return (
    <div className="d-flex justify-content-start align-items-center lesson-item-search">
      <button className="btn btn-info mx-2 p-0" onClick={() => onZoom(lesson)}>zoom</button>
      <span
        onClick={() => onModal(payload)}
        className="card-text mb-2 mt-2 fst-italic text-nowrap text-decoration-underline lesson-item-text"
      >
        {lesson.name}
      </span>
    </div>
  );
}

LessonItem.propTypes = {
  lesson: PropTypes.object,
  onZoom: PropTypes.func,
  onModal: PropTypes.func
};

export default LessonItem;
